<template>
    <div class="bg">
        <el-row type="flex" class="row-bg" align="middle">
            <el-col :md="{offset: 7, span: 10}" :sm="{offset: 6, span: 12}" :xs="{offset: 2, span: 20}">
                <el-card class="card">
                    <el-input class="inputs" placeholder="Usu√°rio" v-model="email" ></el-input>
                    <el-input class="inputs" v-model="password" type="password" placeholder="Senha"></el-input>
                    <div class="buttons">
                        <el-button size="small" type="danger">Cancelar</el-button>
                        <el-button size="large" type="success" @click="logon()" >Entrar</el-button>
                    </div>
                </el-card>
            </el-col>
        </el-row>
    </div>
</template>
<script>

    import axios from 'axios';
    export default {
        data() {
            return {
                email: '',
                password: ''
            }
        },
        methods: {
            logon() {
                axios.post("api/login", {email, password})
                    .then(response => {
                        console.log("RESPOSTA: ");
                        console.log(response);
                    })
                     .catch(error => {
                         console.log("ERRO: ");
                        console.log(error);
                     });
                     
            }
        },
        mounted() {
            console.log("MOUNTED");
        }
    }
</script>
<style scoped>

    .row-bg {
        height: 100vh;
        background-color: #1f1f1f; /*#cccccc */
    }

    .card {
        background-color:  #3f3f3f; /*#404555; /*#6a6a6a; /*#afaacc */
        border: none;
    }
    .inputs {
        margin-bottom: 10px;
    }

    .buttons {
        display: flex;
        justify-content: space-between;
    }
</style>


